$default-font-size: 16px;

.main-main
{
    .main
    {
        .html
        {
            margin: 20px 0px 0px 0px; 
             
            .alignleft
            {
                float: left;
                margin-right: 15px;
                border: 0px solid #ffffff;
                border-radius: 15px;
            }

            p
            {
                font-family: 'Myriad Pro', 'Sans-Serif';
                font-style: normal;
                font-weight: normal;
                color: #000000;
                font-size: $default-font-size + 2;
                margin: 0px;
                align-items: center;
            }

            table
            {
                border: 0;
                margin: 25px 0px;

                tbody
                {
                    border: 0;
                }

                tr
                {
                    border: 0;

                    td
                    {
                        border: 0;
                        padding: 5px 0px;

                        font-family: 'Exo 2', 'Sans-Serif';
                        font-style: normal;
                        font-weight: normal;
                        color: #000000; 
                        font-size: $default-font-size;


                        &:nth-of-type(1)
                        {
                            padding-left: 20px;
                        }

                        &:nth-last-of-type(1)
                        {
                            padding-right: 20px;
                        }
                    }

                    &:nth-of-type(1)
                    {
                        border-bottom: 1px solid #8c8c8c;

                        td
                        {
                            padding-top: 15px;
                            padding-bottom: 15px;
                            font-weight: bold;
                        }
                        
                    }
                }
            }
        }

        .siteurl
        {
            width: 95%;
            justify-content: flex-start;
            text-decoration: none;
            
            p
            {
                width: 40px;
                height: 20px;
                background-color: #8ad5dd;
                font-family: 'Exo 2', 'Sans-Serif';
                font-style: normal;
                font-weight: bold;
                color: #ffffff; 
                font-size: $default-font-size - 4;
                text-align: center;
                border: 0px solid #ffffff;
                border-radius: 5px 0px 0px 5px;
                text-decoration: none;
                margin: 25px 0px 15px 0px;

                &:nth-last-of-type(1)
                {
                    width: 100px;
                    height: 20px;
                    background-color: #e8e8e8;
                    color: #8ad5dd;
                    border-radius: 0px 5px 5px 0px;
                }
            }
        }

        .single-head
        {
            width: 95%;
            justify-content: space-between;
            align-items: center;
            
            .head
            {
                align-items: center;

                img
                {
                    max-width: 50px;
                    max-height: 50px;
                    margin-right: -10px;
                    z-index: 1;
                }
                p
                {
                    width: 450px;
                    height: 30px;
                    font-family: 'Exo 2', 'Sans-Serif';
                    font-style: normal;
                    font-weight: bold;
                    color: #ffffff; 
                    font-size: $default-font-size + 1;
                    text-align: center;
                    padding-left: 20px;
                    background-color: #6f6f6f;
                    border: 0px solid #ffffff;
                    border-radius: 5px;
                    justify-content: flex-start;
                    align-items: center;
                    margin: 0px;
                }
            }

            .rating
            {
                width: 55px;
                height: 35px;
                justify-content: center;
                align-items: center;
                font-family: 'Exo 2', 'Sans-Serif';
                font-style: normal;
                font-weight: bold;
                color: #ffffff; 
                font-size: $default-font-size;
                text-align: center;
                background-color: #00a000;
                border: 1px solid #bababa;
            }
        }

        .single-content
        {
            width: 95%;
        }
    }
}
